* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  min-height: 100vh;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  transition: background-color 0.6s ease;
  position: relative;
  font-family: Arial, sans-serif;
}

.hadith-box {
  max-width: 700px;
  width: 100%;
  background: white;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  font-size: 1.2rem;
  line-height: 1.6;
  margin-bottom: 100px;
}

.next-btn {
  position: fixed;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  padding: 1rem 2rem;
  background-color: #0056b3;
  color: white;
  border: none;
  font-size: 1rem;
  border-radius: 30px;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: background-color 0.3s ease;
}

.next-btn:hover {
  background-color: #376699;
}
